

/* General styles for the app */
body {
  margin: 0;
  font-family: 'Roboto', sans-serif; /* Use Google Fonts */
  background: linear-gradient(to bottom, #ffefba, #ffffff); /* Light gradient background */
  height: 80vh;
  overflow: scroll;
}

/* Navigation menu styles */
nav {
  position: fixed;
  top: 0;
  width: 100%;
  background-color: #333; /* Solid background for nav */
  color: #fff;
  padding: 10px 20px;
  display: flex;
  justify-content: center;
  align-items: center;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  z-index: 1000;
}

nav a {
  color: #fff;
  text-decoration: none;
  margin: 0 15px;
  font-size: 1.2rem;
}

nav a:hover {
  text-decoration: underline;
}

.header-container {
  margin-top: 6%;
}

/* Ensure the components occupy the full viewport height */
main, .container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  min-height: 90vh;
  box-sizing: border-box;
  width: 90%; /* Set the content width */
  margin: 0 auto; /* Center horizontally */
}

#root > main > section.todo-list-container {
  width: 90%;
}

/* Gradient background for components */
.container {
  background: linear-gradient(to bottom, #ffffff, #f1f1f1);
  padding: 20px; /* Add some padding */
  border-radius: 10px; /* Slightly rounded corners */
}

#root > main > section > div > div.add-new-container {
  display: inline-flexbox;
  flex-direction: row;
  justify-content: space-around;
  align-items: center;
}

/* Buttons and inputs */
button {
  background-color: #4caf50; /* Green button */
  color: white;
  padding: 10px 15px;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  font-size: 1rem;
}

.add-btn {
  background-color: #ff2e2e;
  width: 45%;
}

.add-btn:hover {
  background-color: #07ba07;
  width: 45%;
}

.reset-btn {
  background-color: #ff2e2e;
  width: 45%;
}

.reset-btn:hover {
  background-color: #07ba07;
  width: 45%;
}

button:hover {
  background-color: #45a049; /* Darker green on hover */
}

input[type="text"], textarea {
  padding: 10px;
  margin: 10px 0;
  border: 1px solid #ccc;
  border-radius: 5px;
  font-size: 1rem;
  width: 100%; /* Make input fields span full container width */
  max-width: 500px; /* Limit the maximum width */
}

textarea {
  resize: none;
}

/* Footer styles */
footer {
  width: 100%;
  text-align: center;
  padding: 10px;
  background-color: #333; /* Match navigation menu background */
  color: #fff;
  position: relative;
  bottom: 0;
  font-size: 0.9rem;
}
